
.TodoItem {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    border-bottom: 1px solid #ececec;
    /* box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15); */
  }
  
  .TodoItem-p {
    margin: 18px 0 18px 0;
    width: calc(100% - 120px);
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    padding-left: 0;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
 